// 
import React, { PureComponent } from 'react';
import { Row, Col, Card, Divider } from 'antd';
import search from './search';
import SearchComponent from '../../public/SearchComponent';
import btnGroup from './btnGroup';
import BtnGroupComponent from '../../public/BtnGroupComponent';
import columns from './columns';
import TableComponent from '../../public/TableComponent';
import Form from './Form';
import ModalComponent from '../../public/ModalComponent';
import { initState } from '../../../const/initState';
import {
  houseCar as api,
  houseCarBrand as brandApi,
} from '../../../const/api';
import { axiosPost } from '../../../utils/request';
import { connect } from 'react-redux';

@connect(state => state.loading, null)
export default class HouseCar extends PureComponent {
  constructor() {
    super();
    this.state = { ...initState, brandList: [] };
  }

  // 
  componentDidMount() {
    const { pagination } = this.state;
    axiosPost(api.get, { page: pagination.current }, data => {
      if (data.hasOwnProperty('data')) {
        axiosPost(brandApi.all, {}, brand => {
          this.setState({
            brandList: brand.data,
            dataSource: data.data.list,
            pagination: { ...pagination, total: data.data.total },
          });
        });
      }
    }, false);
  }

  // 
  rowSelect = record => this.setState({ record });

  // 
  searchSubmit = values => {
    const { pagination, search } = this.state;
    axiosPost(api.get, { ...values, currentPage: 1 }, data => this.setState({
      dataSource: data.data.list,
      pagination: { ...pagination, total: data.data.totalSize, current: 1 },
      search: { ...search, ...values },
    }), false);
  };

  // 
  searchClear = () => this.setState({ search: {} });

  // 
  handleTableChange = (page, filters, sorter) => {
    const { pagination, search } = this.state;
    const data = { ...search, currentPage: page.current };
    const callback = data => this.setState({
      dataSource: data.data.list,
      pagination: {
        ...pagination,
        total: data.data.total,
        current: page.current,
      },
    });
    axiosPost(api.get, data, callback, false);
  };

  // 
  showModal = title => this.setState({ modalVisible: true, modalTitle: title });

  // 
  add = values => {
    const { dataSource } = this.state;
    const callback = data => {
      dataSource.push(data.data);
      this.setState({
        dataSource: [...dataSource],
        record: data.data,
        modalVisible: false,
      });
    };
    axiosPost(api.add, values, callback, true);
  };

  // 
  edit = values => {
    const { dataSource } = this.state;
    const callback = data => {
      for (let i in dataSource) {
        if (dataSource[i].id === values.id) {
          dataSource[i] = { ...dataSource[i], ...data.data };
          this.setState({
            dataSource: [...dataSource],
            record: dataSource[i],
            modalVisible: false,
          });
          break;
        }
      }
    };
    axiosPost(api.update, values, callback, true);
  };

  // 
  deleteRow = record => {
    const callback = () => {
      const { dataSource } = this.state;
      for (let i in dataSource) {
        if (dataSource[i].id === record.id) {
          dataSource.splice(i, 1);
          this.setState({ dataSource: [...dataSource], record: {} });
          break;
        }
      }
    };
    axiosPost(api.remove, { id: record.id }, callback, true);
  };

  // 
  renderModal = () => {
    const { modalTitle, record, loading, brandList } = this.state;
    switch (modalTitle) {
      case '':
        return <Form add={this.add} loading={loading} brandList={brandList} />;
      case '':
        return <Form record={record} edit={this.edit} loading={loading} brandList={brandList} />;
      default:
        return null;
    }
  };

  render() {
    const { loading } = this.props;
    const { dataSource, pagination, modalVisible, modalTitle, record } = this.state;
    const table = { columns, dataSource, pagination, loading };
    return (
      <Card>
        <Row>
          <Col span={8} style={{ marginTop: -4 }}>
            <SearchComponent
              search={search}
              loading={loading}
              searchSubmit={this.searchSubmit}
              searchClear={this.searchClear}
            />
          </Col>
          <Col span={8}>
            <BtnGroupComponent
              deleteRow={this.deleteRow}
              showModal={this.showModal}
              btnGroup={btnGroup}
              record={record}
            />
          </Col>
        </Row>
        <Divider dashed />
        <TableComponent
          table={table}
          record={record}
          onChange={this.handleTableChange}
          rowSelect={this.rowSelect} />
        <ModalComponent
          width={1200}
          title={modalTitle}
          visible={modalVisible}
          onCancel={() => this.setState({ modalVisible: false })}>
          {this.renderModal()}
        </ModalComponent>
      </Card>
    );
  }
}





