import { houseCar as fieldName } from '../../../const/fieldName';

export default [
  { title: '', dataIndex: fieldName.houseCarName },
  { title: '', dataIndex: fieldName.vehicleBrand, width: 160 },
  { title: '', dataIndex: fieldName.price, width: 140 },
  { title: '', dataIndex: fieldName.spec, width: 140 },
  { title: '', dataIndex: fieldName.gears, width: 140 },
  {
    title: '',
    dataIndex: fieldName.isSencond,
    width: 100,
    render: isSencond => isSencond === 'Y' ? '' : '',
  },
  {
    title: '',
    dataIndex: fieldName.isSales,
    width: 100,
    render: isSales => isSales === 'Y' ? '' : '',
  },
  {
    title: '',
    dataIndex: fieldName.functionType,
    width: 140,
    render: functionType => {
      switch (functionType) {
        case 1:
          return '';
        case 2:
          return '';
        case 3:
          return '';
        default:
          return null;
      }
    },
  },
];



